@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  src: local("Open Sans Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  src: local("Open Sans Light"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
}
img:not([alt]),
label:not([for]),
input[type=submit]:not([value]) {
  outline: 2px solid red;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}
img {
  max-width: 100%;
}
body {
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: "Open Sans", Arial, sans-serif;
  background-color: #ffffff;
}
.main-header {
  background-color: rgba(0, 0, 0, 0.3);
}
.main-logo__img:hover {
  fill: #d22856;
}
.main-nav {
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  z-index: 2;
  /*нет JS, кнопка скрыта*/
  /*нет JS, меню д.б.открыто*/
}
.main-nav--closed {
  position: absolute;
}
.main-nav--opened {
  background-color: #283645;
}
.main-nav__header {
  display: flex;
  padding: 20px;
  padding-bottom: 21px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.99);
  justify-content: space-between;
}
.main-nav__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}
.main-nav--opened .main-nav__menu {
  display: flex;
  background-color: #283645;
  flex-direction: column;
}
.main-nav__item {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  border-top: 1px solid rgba(71, 83, 96, 0.99);
  border-bottom: 1px solid rgba(0, 0, 0, 0.99);
}
.main-nav__link {
  text-decoration: none;
  color: #ffffff;
}
.main-nav__toggle {
  position: absolute;
  width: 50px;
  height: 50px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  top: 10px;
  right: 20px;
  outline: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 21px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 21px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 21px 0 0 rgba(210, 40, 86, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 21px;
  right: -4px;
  width: 30px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__menu {
  display: flex;
}
@media (min-width: 320px) {
  .main-nav--closed .main-nav__menu {
    display: none;
  }
}
@media (min-width: 320px) and (min-width: 660px) {
  .main-nav__header {
    min-height: 110px;
    padding-top: 27px;
  }
  .main-nav__toggle {
    top: 32px;
  }
}
@media (min-width: 320px) and (min-width: 1200px) {
  .main-nav {
    display: flex;
    padding-bottom: 0;
    height: 110px;
    max-width: 1200px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__header {
    display: inline-block;
    border-bottom: none;
    padding: 0;
    margin-left: 130px;
    margin-top: 27px;
    margin-right: 173px;
  }
  .main-nav__menu {
    flex-direction: row;
    margin-top: 18px;
  }
  .main-nav__item {
    border-top: none;
    border-bottom: none;
    margin-right: 80px;
  }
  .main-nav__link {
    text-transform: none;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
  }
  .main-nav--closed .main-nav__menu {
    display: flex;
    margin-top: 23px;
  }
}
